* {
  box-sizing: border-box;
}

*:before, *:after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-family: "ubunturegular", "Arial", sans-serif, sans-serif;
  font-size: 20px;
  line-height: 1.5;
  color: #fff;
  background-color: #3e3f40;
  display: flex;
  flex-direction: column;
}

body #shadow {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 55;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: all .3s;
}

body #shadow.active {
  visibility: visible;
  opacity: 1;
}

body .hide {
  display: none !important;
}

body.open-mobile-drop #mobile-drop {
  left: 0;
}

body.open-filter #filter {
  left: 0;
}

html,
body {
  height: 100%;
  margin: 0;
}

input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  outline: none;
}

a {
  text-decoration: underline;
  outline: none;
  color: #fff;
}

a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

table {
  width: 100%;
}

label {
  cursor: pointer;
}

svg {
  transition: all .3s;
}

