.form {
  position: relative;
}

.form .form-group {
  display: flex;
  align-items: flex-start;
  margin: 7px 0;
}

.form .control-label {
  line-height: 1;
  margin: 0 7px 0 0;
  flex-basis: 50%;
}

.form .control-label + div {
  flex-basis: 50%;
  margin: -7px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form .control-label + div label {
  margin: 7px 0;
}

.form .select2 {
  flex-basis: 50%;
}

.form .form-control {
  position: relative;
  height: 40px;
  padding: 0 17px;
  flex-basis: 50%;
  width: 100%;
  border-radius: 4px;
  transition: all .3s;
}

.form .form-control::placeholder {
  transition: all .3s;
}

.form .form-control:focus::placeholder {
  opacity: 0;
}

.form .help-block {
  display: none;
}

.form input,
.form textarea {
  border: 1px solid #999;
  background: transparent;
}

.form textarea.form-control {
  padding: 17px;
  height: 160px;
  resize: none;
}

.form button.form-control {
  flex-basis: auto;
  width: auto;
}

.appearance label {
  display: flex;
  position: relative;
  padding-left: 42px;
  line-height: 26px;
}

.appearance label.control-label {
  padding: 0;
}

.appearance input[type='checkbox'] {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 26px;
  height: 26px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 1px solid #999;
  cursor: pointer;
  padding: 0;
}

.appearance input[type='checkbox']:checked:after {
  content: '';
  background-color: transparent;
  position: absolute;
  left: 50%;
  top: 39%;
  width: 9px;
  border-bottom: 3px solid;
  height: 19px;
  border-right: 3px solid;
  border-color: #000;
  transform: translate(-50%, -50%) rotate(45deg);
}

