}

.contacts__fond:after {
  content: '';
  position: absolute;
  background: rgba(129, 129, 129, 0.5);
  filter: blur(6px);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.contacts__inner {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
